@font-face {
  font-family: "PTsans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PTsans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
  background-color: #f2f2f2;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  font-family: "PTsans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  min-width: 320px;
  max-width: 1200px;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}
.button {
  display: block;
  width: 284px;
  margin: 0 auto;
  padding: 16px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: "PTsans", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496db;
  color: rgba(255, 255, 255, 0.3);
}
.button:focus {
  opacity: 0.8;
}
.button:disabled {
  background-color: #e5e5e5;
  pointer-events: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.navigation {
  display: flex;
  position: relative;
  justify-content: flex-end;
  padding: 17px 20px;
}
@media (min-width: 768px) {
  .navigation {
    display: block;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .navigation--is-closed .navigation__list {
    display: none;
  }
}
.navigation--is-closed .navigation__button::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
@media (max-width: 767px) {
  .navigation--is-opened .navigation__list {
    display: flex;
    z-index: 2;
  }
}
.navigation--is-opened .navigation__button::after,
.navigation--is-opened .navigation__button::before {
  position: absolute;
  content: "";
  width: 23px;
  height: 3px;
  top: 10px;
  background-color: #ffffff;
}
.navigation--is-opened .navigation__button::after {
  transform: rotate(45deg);
  left: 0;
}
.navigation--is-opened .navigation__button::before {
  transform: rotate(-45deg);
  left: 0;
}
@media (max-width: 767px) {
  .navigation--is-opened .navigation__link {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (max-width: 767px) {
  .navigation--no-js {
    flex-direction: column;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .navigation--no-js .navigation__logo {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .navigation--no-js .navigation__list {
    display: flex;
    position: relative;
  }
}
@media (max-width: 767px) {
  .navigation--no-js .navigation__button {
    display: none;
  }
}
.navigation__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.navigation__image {
  max-width: initial;
}
.navigation__list {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  flex-direction: column;
  text-align: center;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: grid;
    grid-template-columns: repeat(4, auto);
    background-color: transparent;
  }
}
@media (max-width: 767px) {
  .navigation__list::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    right: 0;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .navigation__item {
    min-width: 153px;
    max-width: 261px;
  }
}
@media (min-width: 1200px) {
  .navigation__item {
    width: 239px;
  }
}
@media (max-width: 767px) {
  .navigation__item:not(:last-of-type) {
    margin-bottom: 1px;
  }
  .navigation__item:first-of-type {
    box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2);
  }
  .navigation__item:not(:first-of-type) {
    box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
  }
  .navigation__item:last-of-type {
    box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-2) {
    margin-right: 76px;
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(4n-2) {
    margin-right: 120px;
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-1) {
    margin-left: 76px;
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(4n-1) {
    margin-left: 120px;
  }
}
.navigation__link {
  color: #ffffff;
  text-decoration: none;
  display: block;
  padding: 18px 12px 11px;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 18px 18px 17px 18px;
    color: #000000;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
  }
}
@media (min-width: 768px) {
  .navigation__link--current {
    color: #766357;
    pointer-events: none;
  }
}
.navigation__link:hover {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .navigation__link:hover {
    color: #81b3d2;
    background-color: transparent;
  }
}
.navigation__link:active {
  background-color: #5496db;
}
@media (min-width: 768px) {
  .navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }
}
.navigation__button {
  position: relative;
  z-index: 3;
  background-color: transparent;
  border: none;
  width: 22px;
  height: 22px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__button {
    display: none;
  }
}
.navigation__button:hover {
  opacity: 0.6;
}
.navigation__button:active {
  opacity: 0.3;
}
.navigation__button:focus {
  opacity: 0.2;
}
.navigation__button:disabled {
  opacity: 0.1;
  pointer-events: none;
}
